.benefits {
  margin-bottom: 10rem;
  @include mobile {
    margin-bottom: 5rem;
  }
  &__title {
    margin-bottom: 4rem;
    @include mobile {
      margin-bottom: 3rem;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 2.4rem;

    @include mobile {
      display: flex;
      flex-direction: column;
      gap: 1rem;
    }
  }

  &__item {
    grid-column: 6 span;
    &:nth-child(5n + 3) {
      grid-column: 4 span;
    }
    &:nth-child(5n + 4) {
      grid-column: 4 span;
    }
    &:nth-child(5n + 5) {
      grid-column: 4 span;
    }
  }
}

.ben-item {
  padding: 1.5rem 5rem;
  padding-right: 4rem;
  border: 1px solid #b01c32;
  border-radius: 1.2rem;
  display: flex;
  align-items: center;
  gap: 2.5rem;

  @include mobile {
    padding: 1.5rem;
    gap: 1rem;
  }
  &__icon {
    flex-shrink: 0;
    height: 8rem;
    max-width: 10rem;
    @include mobile {
      height: 5.4rem;
      width: 5.4rem;
    }
    img {
      display: block;
      height: 100%;
      width: auto;
      object-fit: contain;
    }
  }
  &__name {
    font-weight: 600;
  }
}
